/* admin_news_and_events_page_header */

#admin_news_and_events_page_header{
	height: 13vh;
	width: 100%;
	position: sticky;
	top: 0;
	left: 0;
	background-color: rgba(0, 0, 0,0.95);
	color: var(--fourth-color);
	z-index: 3;
}

#admin_news_and_events_page_header_nav{
	height: 100%;
	width: 100%;
	display: flex;
	align-items: center;
	justify-content: space-around;
}

#admin_news_and_events_page_header_nav_a1{
	text-decoration: none;
	color: var(--fourth-color);
	padding: 1%;
}
#admin_news_and_events_page_header_nav_a1:hover{
	background-color: var(--fourth-color);
	color: black;
}

#admin_news_and_events_page_header_nav_h1{
